@import url(https://fonts.googleapis.com/css?family=Roboto:400,500,700,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

p, h1, h2, h3, h4, h5, h6{
  font-family: 'Roboto', sans-serif;
}
.my-masonry-grid { /* Not needed if autoprefixing */ /* Not needed if autoprefixing */
  display: flex;
  /* gutter size offset */
  width: auto;
}
.my-masonry-grid_column {
  /* gutter size */
  background-clip: padding-box;
}
.my-masonry-grid_column > div { /* change div to reference your elements you put in <Masonry> */
  margin-bottom: 30px;
}
.content.container{
  margin-top:12vh;
}
#navbar{
    padding-top:5px;
    display: flex;
    flex-direction: row;
    height: 65px;
    background:white;
   
}
.nav{
    width: 100%;
    top: 0;
    position: fixed;
    z-index: 9999;
    
}

 hr{
     background: #8e8e8e;
    width: 100%;
    border-top: 1px solid #efefef !important;
    display: block;
    z-index: 9999;
    margin-top: 0 !important;
    margin-bottom: 0 !important;
    border-bottom: 0;
    border-left: 0;
    /* unicode-bidi: isolate;
    margin-block-start: 0.5em;
    margin-block-end: 0.5em;
    margin-inline-start: auto;
    margin-inline-end: auto;
    overflow: hidden;
    border-style: inset; */
    /* border-width: 1px; */
}
#navbar img{
    max-width: 100%;
    height: auto;
    justify-content: center;
}
#navbar .logo{
    padding: 10px;
    height: 48px;
  
   
    border-radius: 50%;
    margin-top:3px;
    margin-left: 15px;
  
    margin-right: 15px;
    width: 48px;
    display: flex;
}
#navbar .logo:hover{
    background: rgba(0, 0, 0, .06);
}
#navbar .border{
    height:30px;
    width:2px;
    border-left:2px solid #efefef;
    box-sizing:border-box;
    margin-left:16px;
    display: block;
}
#navbar .search{ 
    margin-top: 5px;
    height: 70%;
    border-top-left-radius: 8px;
    border-bottom-left-radius: 8px;
    border-top-right-radius: 8px;
    border-bottom-right-radius: 8px;
    display: flex;
    flex-basis: 55%;
    background: #efefef;
    
}
.icon-search{
    padding-top:6px;
    padding-left:10px;
    flex-basis: 6%;
}
.icon-search img{
    width: 58%;
}

#navbar input{
    border-top-right-radius: 8px;
    border-bottom-right-radius: 8px;
    height: 100%;
    width: 100%;
    background: #efefef;
    border:0;
   
}
::-webkit-input-placeholder { /* Chrome/Opera/Safari */
    font-weight: bold;
    font-size:16px;
  }
  ::-moz-placeholder { /* Firefox 19+ */
    font-weight: bold;
    font-size:16px;
  }
  :-ms-input-placeholder { /* IE 10+ */
    font-weight: bold;
    font-size:16px;
  }
  :-moz-placeholder { /* Firefox 18- */
    font-weight: bold;
    font-size:16px;
  }
#navbar .btn{
    margin-top: 5px;
    /* flex-basis: 7%; */
}
#navbar .add{
    flex-basis: 5%;
    margin-top:2px;
    margin-left:0px;
    display: flex;
    padding:7px;
    height: 48px;
    width: 48px;
    background: transparent;
    border-radius:50%;
}
#navbar .border{
    
    margin-top: 10px;
}
#navbar .inicio{
    color:#333;
   margin-bottom: 10px;
    margin-left:10px;
    flex-basis: 5%;
    background: transparent;
    font-weight: bold;
    font-size: 16px;
    border-radius:20px;
    padding-left: 5px;
    padding-right: 5px;
    padding-top: 8px;
    padding-bottom: 8px;
}
#navbar .inicio:hover,#navbar .siguiendo:hover,#navbar .user:hover{
    background: #efefef;
}
#navbar .siguiendo{
    color:#8e8e8e;
    margin-bottom: 10px;
    margin-left:10px;
    flex-basis: 8%;
    background: transparent;
    font-weight: bold;
    border-radius:20px;
    padding-left:5px;
    padding-right: 5px;
    padding-top: 8px;
    padding-bottom: 8px;
}
#navbar .user{
    color:#8e8e8e;
    margin-bottom: 10px;
    margin-left:10px;
    display: flex;
    flex-basis: 8%;
    background: transparent;
    font-weight: bold;
    border-radius:20px;
    padding-left: 5px;
    padding-right: 5px;
    padding-top: 8px;
    padding-bottom: 8px;
}
#navbar .userPic{
    margin-left:4px;
    height: 24px;
    width: 24px;
    border-radius:50%;
    background: #8e8e8e;
    color: white;
}
#navbar .userName{
    margin-left:8px;
}
#navbar .message,#navbar .news, #navbar .add{
    flex-basis: 4%;
}
#navbar .message{
    margin-top:2px;
    margin-left:20px;
    display: flex;
    padding:7px;
    height: 48px;
    width: 48px;
    background: transparent;
    border-radius:50%;
    
}
#navbar .message:hover,#navbar .news:hover,#navbar .add:hover{
    background: #efefef;
}
#navbar .news{
    margin-top:2px;
    margin-left:0px;
    display: flex;
    padding:7px;
    height: 48px;
    width: 48px;
    background: transparent;
    border-radius:50%;
}
#navbar .message img{
    margin-left:5px;
    width: 70%;
}
#navbar .news img{
    margin-left:5px;
    width: 70%;
}
#navbar .add img{
    margin-left:5px;
    width: 70%;
}
@media (max-width: 425px) {
    #navbar .userName,#navbar .message,#navbar .news,#navbar .add, #navbar .border,#navbar .siguiendo,#navbar .inicio{
        display: none;
    }
    
}
@media (min-width: 426px) and (max-width: 768px) {
  #navbar .siguiendo{
        display: none;
    }
    #navbar .message img{
        margin-left: 0px;
        width: 90%;
    }
    #navbar .news img{
        margin-left:0px;
        width: 100%;
    }
    #navbar .message{
      
        margin-left: 5px;
       
    }
    #navbar .user{
        margin-left: 0px;
    }
    #navbar .border {
        margin-left: 5px;
    }
    .icon-search img {
        width: 100%;
    }

}

 .imageItem{
    width: 260px;
    padding-bottom: 42px;
    padding-left: 8px;
    padding-right: 8px;
    padding-top:8px;
    border-radius:10px;
    position: relative;
}
.imageItem:hover{
    background: #efefef;
}
.imageItem .img{
    border-radius:10px;
    width: 100%;
   
}
.imageItem .img:hover{
    cursor: -webkit-zoom-in;
    cursor: zoom-in;
    -webkit-filter:brightness(0.7);
            filter:brightness(0.7);
}
 .imageItem button{
     height: 40px;
     border-radius: 8px;
     
     font-weight: bold; 
     width: 105px;
     border: none;
     color: white;
     background: #c92228;
     display:flex;
     position: absolute;
    
   
} 
.imageItem button:hover{
    -webkit-filter:brightness(0.7);
            filter:brightness(0.7);
}
.imageItem .pin{
  
    width: 18%;
    margin-right: 5px;
}
.dots{
    /* position: absolute; */
    width: 60%;
    margin-left:5px;
    margin-bottom:3px;
    /* right: 10px;
    bottom: 15px;
    cursor: pointer; */
}
.circleDots{
    
    position: absolute;
    width: 5%;
    right: 10px;
    bottom: 10px;
    cursor: pointer;
    height: 24px;
    width: 24px;
    border-radius: 50%;
 
}
.circleDots:hover{
    background: rgba(0, 0, 0, .06);
}
.share{
    border-radius:50%;
    height: 40px;
    width: 40px;
    background: #333;
    position: absolute;
    right: 15px;
    bottom: 48px;
    cursor: pointer;
}
.share img{
    width: 100%
}

