.modal-content{
    margin-top: 90px;
    position: relative;
    border: inherit;
}

.modal-header{ 
    border-bottom: none;
    display: flex;
    flex-direction: row;
  

}
.modal-content .save{
    height: 40px;
    border-radius: 8px;
    font-size: 16px;
    font-weight: bold; 
    width: 105px;
    border: none;
    color: white;
    background: #c92228;
    display:flex;
   
   
  
} 
.modal-content .save:hover{
    -webkit-filter:brightness(0.7);
            filter:brightness(0.7);
}

.modal-content .pin{
  
    width: 18%;
    margin-right: 5px;
}
.modal-content .sent{
    padding-top:1px;
    margin-right: 10px;
    height: 40px;
    border-radius: 8px;
    font-size: 16px;
    font-weight: bold; 
    width: 105px;
    border: none;
    color: #333;
    background: #efefef;
    display:flex;
}
.modal-content .sent:hover{
    background: rgb(226, 226, 226);
}
.modal-content .shar{
   margin-top:-3px;
    width: 32%;
    margin-right: 5px;
}
.modal-header .add{



    flex-basis: 5%;
    margin-top: 0;
    margin-right: 40vw;
    padding:7px;
    height: 40px;
    width: 40px;
    background: transparent;
    border-radius:50%;
    
}
.modal-header .add:hover{
    background: rgba(0, 0, 0, 0.06)
}
.modal-header .add img{
    margin-left: 4px;
    margin-bottom: 1px;
    width: 70%;
}
.arrow{
    cursor: pointer;
    position: absolute;
    top:-10px;
    left: -17vw;
    /* margin-left: -4vw;
    flex-basis: 5%; */
}
.arrow img{
    
    width: 5%;
}
.closed{
    cursor: pointer;
    position: absolute;
    top:-10px;
    right: -33vw;
}
.closed img{
    width:9%;
}






.modal.fade.show{
 
    background: #efefef
}
.modal-body{
    padding-top: 0px;
}
.modal-body img{
    max-width: 50%;
    border-radius: 8px;
}
.modal-footer{
    border-top:inherit;
}
